<template>
  <div id="signup">
    <img src="@/assets/logo.png"/>
    <div id="signup-box">
      <div class="form-group">
        <label></label>
        <input type="text" class="form-control" v-model="name"/>
      </div>
      <div class="form-group">
        <label></label>
        <input class="form-control" v-model="email"/>
        <span v-show="errorMessage.email!==''" class="validation_warn">
          {{errorMessage.email}}</span>
      </div>
      <div class="form-group">
        <label></label>
        <input type="password" class="form-control" v-model="password"/>
        <span v-show="errorMessage.password!==''" class="validation_warn">
          {{errorMessage.password}}</span>
      </div>
      <div class="form-group">
        <label></label>
        <input type="date" class="form-control" v-model="birthDate"/>
        <span v-show="errorMessage.birthDate!==''" class="validation_warn">
          {{errorMessage.birthDate}}</span>
      </div>

      <div class="form-group">
        <button class="btn btn-default" @click="_signUp"></button>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      email: '',
      password: '',
      name: '',
      birthDate: '',
      errorMessage: Object,
    };
  },
  watch: {
    email() {
      const EMAIL_REGEX = RegExp('[^@ ]+@[^@ ]+\\.[^@ ]+');
      if (!EMAIL_REGEX.test(this.email)) {
        this.errorMessage.email = '  .';
        return;
      }
      this.errorMessage.email = '';

      if (this.email.length < 3 || this.email.length > 40) {
        this.errorMessage.email = '  3()  40   .';
        return;
      }
      this.errorMessage.email = '';
    },
    password() {
      if (this.password.length < 6 || this.password.length > 20) {
        this.errorMessage.password = '  6()  20   .';
      } else {
        this.errorMessage.password = '';
      }
    },
    birthDay() {
      if (this.birthDate.length < 6 || this.birthDate.length > 10) {
        this.errorMessage.birthDate = '   .';
      } else {
        this.errorMessage.birthDate = '';
      }
    },
  },
  methods: {
    validation() {
      const validation = Object.keys(this.errorMessage).every(x => this.errorMessage[x] === '');
      if (!validation) {
        alert('  .');
        return false;
      }
      if (this.name.length === 0 || this.password.length === 0 || this.email.length === 0) {
        alert('form .');
        return false;
      }
      return true;
    },
    _signUp() {
      if (!this.validation()) {
        return;
      }
      axios.post('http://192.168.0.32:8080/api/auth/signup', {
        email: this.email,
        name: this.name,
        birthDate: this.birthDate,
        password: this.password,
      }).then((response) => {
        if (response.status === 201) {
          alert(' ');
          this.$router.push('check');
        }
      }).catch((error) => {
        console.log(error);
      });
    },
  },
};
</script>

<style scoped>
  #signup{
    text-align: center
  }
  #signup-box{
    border: 1px solid;
    padding: 10px;
    box-shadow: 2px 3px #888888;
    margin: auto;
    width:300px;
  }
  .validation_warn{
    color:red;
  }
</style>



